<template>
    <div class="business_vouchers_describe">
      <!--  头部  -->
      <div class="first_line">
        <van-icon name="arrow-left" class="arrow-left pull-left" @click="goBack()"/>
        <span class="title_name">代金券说明</span>
      </div>
      <div class="main">
        <div>
          <h3>Q1: 什么是商家代金券？</h3>
          <p>商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。</p>
          <h3>Q2: 怎么获得商家代金券？</h3>
          <ul>
          <li>进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。</li>
            <li>「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。
            </li>
          </ul>
          <h3>Q3: 商家代金券使用条件</h3>
          <ul>
            <li>商家代金券仅限在指定商家使用</li>
            <li>商家代金券仅限在线支付使用</li>
            <li>每个订单只能使用一张商家代金券，不可与其他代金券叠加使用</li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "BusinessVouchersDescribe",
      methods:{
        goBack(){
          window.history.go(-1);
        }
      },
    }
</script>

<style scoped>
  .business_vouchers_describe{
    height: 100%;
  }
  .first_line{
    line-height: 4rem;
    text-align: center;
    background: #3190e8;
    width: 100%;
  }
  .arrow-left{
    line-height: 4rem;
    color: white;
    font-size: 2rem;
  }
  .title_name{
    color: white;
    font-size: 2rem;
    text-align: center;
  }

  .main{
    padding: 0.5rem 1rem 0 1rem;
  }
  h3{
    font-size: 1.7rem;
    color: #666;
  }
</style>



